const mainMenu = document.querySelector("#site-navigation");
const subMenu = document.querySelector(".sub-menu");
const subMenuBtn = document.querySelector(".with-sub-menu");

const menuItems = document.querySelectorAll("#primary-menu li");
const subMenuItems = document.querySelectorAll(".sub-menu li");
const hamburgerIcon = document.querySelector(".hamburger-icon");
const hamburgerIconLines = hamburgerIcon.querySelectorAll(".hamburger-line");

let subMenuBackBtn = document.createElement("div");
subMenuBackBtn.classList.add("sub-menu-back-btn");

subMenu.append(subMenuBackBtn);

const toggleSubMenu = () => {
  subMenu.classList.toggle("active");
};

const toggleMainMenu = () => {
  mainMenu.classList.toggle("active");
  if (subMenu.classList.toggle("active")) {
    subMenu.classList.toggle("active");
  }
  transformHamburgerIcon();
};

const transformHamburgerIcon = () => {
  hamburgerIconLines.forEach((line) => {
    line.classList.toggle("active");
  });
};

subMenuBtn.addEventListener("click", toggleSubMenu);
hamburgerIcon.addEventListener("click", toggleMainMenu);
menuItems.forEach((item) => {
  if (!item.classList.contains("with-sub-menu")) {
    item.addEventListener("click", toggleMainMenu);
  }
});

